@font-face {
  font-family: "cfont";
  src: url(./webfonts/myfont.ttf);
  font-style: normal;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Mainblock {
  margin: 0 !important;
  padding: 0 !important;
  background: url(./images/BG-green.png) no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  position: absolute;
  display: block;
  max-height: 100vh;
  height: 100%;
  width: 100%;
  overflow: scroll;
}

.page-footer {
  position: absolute;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: #d0eee8 !important;
  padding: 5px 0 5px 0;
}

.root {
  margin-bottom: 100px;
}

.user-conf-btn {
  position: absolute;
  z-index: 1;
  font-size: 2vh;
  padding: 0.6vh 0.8vh;
  padding-bottom: 1vh;
  padding-right: 0.5vh;

  border: 2px solid #bdc3c7;
  top: 1;
  left: 1;
  border-radius: 50%;
  cursor: pointer;
}

.info-btn {
  position: absolute;
  z-index: 1;
  font-size: 2vh;
  padding: 0.5vh 1.1vh;
  padding-bottom: 0.8vh;
  padding-right: 1.1vh;

  border: 2px solid #bdc3c7;
  top: 1;
  right: 1;
  border-radius: 50%;
  cursor: pointer;
}
