.reset-card {
  border-radius: 30px;
  border: 4px solid #1dd1a1;
  margin-top: -250px !important;
  padding: 10px;
  padding-left: 20px;
}

.reset-card .card-body > h1 {
  padding: 10px;
}

.reset-card .card-body .form-group .input-field {
  margin-left: 10px;
  margin-bottom: 20px;
  width: 500px !important;
}

.form-control {
  max-width: 450px;
  width: auto;
  height: 55px;
  background-color: #f0f0f0;
  margin: 10px 0;
  border-radius: 55px;
  display: grid;
  grid-template-columns: 15% 85%;
  padding: 0 0.4rem;
  margin: auto;
}

.form-control i {
  text-align: center;
  line-height: 55px;
  color: #acacac;
  font-size: 1.1rem;
}

.form-control input {
  background: none;
  outline: none;
  border: none;
  line-height: 1;
  font-weight: 600;
  font-size: 1.1rem;
  color: #333;
}

.form-control input::placeholder {
  color: #aaa;
  font-weight: 500;
}

.card-body .button2 {
  margin-left: 30px;
  height: 40px;
  width: 120px;
  font-size: 1.2rem;
  line-height: 15px;
}

.card-body .button3 {
  margin-left: 30px;
  font-size: 1rem;
  line-height: 28px;
}
