.login-card {
  border-radius: 30px;
  border: 4px solid #1dd1a1;
  top: 50%;
  margin-top: -120px !important;
  overflow: visible;
  width: 400px;
}

.input-field {
  max-width: 450px;
  width: auto;
  height: 55px;
  background-color: #f0f0f0;
  margin: 10px 0;
  border-radius: 55px;
  display: grid;
  grid-template-columns: 15% 85%;
  padding: 0 0.4rem;
  margin: auto;
}

.input-field i {
  text-align: center;
  line-height: 55px;
  color: #acacac;
  font-size: 1.1rem;
}

.input-field input {
  background: none;
  outline: none;
  border: none;
  line-height: 1;
  font-weight: 600;
  font-size: 1.1rem;
  color: #333;
}

.input-field input::placeholder {
  color: #aaa;
  font-weight: 500;
}

.card-header {
  font-weight: 600;
  font-size: 2.4rem;
}

.button {
  height: 50px;
  width: 120px;
  border-radius: 10px;
}
